.item {
  flex-basis: calc(100% / 5);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-top: 1px solid grey;
}
.item:not(:last-child) {
  border-right: 1px solid grey;
}
.label {
  font-size: 14px;
}
.percentage {
  font-size: 16px;
}
